.rentalRequest {

  &__wrapper {}

  &__procces {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
  }

  &__title {
    text-align: center;
    margin: 0px 0px 20px 0px; 
  }

  &__form {
    position: relative;
    & span {
      position: absolute;
      top: -65px;
      left: -10px;
    }
  }
}