.rentalSelectedBike {

   &__wrapper {}

   &__body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
   }

   &__search {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr;
      column-gap: 40px;
      row-gap: 40px;
      margin: 0px 0px 40px 0px;
      @media (max-width: 1270px) {
         grid-template-columns: 1fr 1fr 1fr;
      }
      @media (max-width: 950px) {
         grid-template-columns: 1fr 1fr;
      }
      @media (max-width: 650px) {
         grid-template-columns: 1fr;
      }
   }

   &__search-item {
      display: flex;
      justify-content: center;
      align-items: center;
   }

   &__search-type{
      min-width: 260px;
      max-width: 345px;
      width: 100%;
   }

   &__brend {
      position: relative;
      z-index: 10;
   }

   &__sizeFrame {
      
   }

   &__hideSelection {
      display: none;
   }

   &__hideSelection-active {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 62px;
      z-index: 50;
      border-radius: 15px;
      background-color: rgba(255, 255, 255, 0.418);
   }

   &__signature {
      margin: 0px 0px 15px 0px;
   }

   &__options {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr;
      column-gap: 40px;
      row-gap: 40px;
      margin: 0px 0px 40px 0px;
      @media (max-width: 1270px) {
         grid-template-columns: 1fr 1fr 1fr;
      }
      @media (max-width: 950px) {
         grid-template-columns: 1fr 1fr;
      }
      @media (max-width: 650px) {
         grid-template-columns: 1fr;
      }
   }



   &__card {
      display: flex;
      justify-content: center;
      align-items: center;
   }

   &__select{
      max-width: 260px;
      width: 100%;
      height: 60px;
   }
}