.requestForm {

   &__wrapper{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: start;
   }

   &__body {
      margin: 0px -20px;
      margin-bottom: 40px;
      display: flex;

      @media (max-width: 901px) {
         flex-direction: column;
      }
   }

   &__button {
      width: 260px;
      height: 60px;
   }

   &__column {
      flex: 1 1 50%;
      padding: 0px 20px;
   }

   &__contact-info {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr;
      column-gap: 40px;
      grid-template-areas:
         "signName signName"
         "name name"
         "phone phone"
         "signAdress signAdress"
         "adress adress"
         "signPay signPay"
         "online onplace";

      @media (max-width: 901px) {
         margin: 0px 0px 35px 0px;
         grid-template-areas:
            "signName signName"
            "name phone"
            "signAdress signAdress"
            "adress adress"
            "signPay signPay"
            "online onplace";
      }

      @media (max-width: 471px) {
         margin: 0px 0px 35px 0px;
         grid-template-areas:
            "signName signName"
            "name name"
            "phone phone"
            "signAdress signAdress"
            "adress adress"
            "signPay signPay"
            "online online"
            "onplace onplace";
      }
   }

   &__signature-name {
      grid-area: signName;
   }

   &__name {
      margin: 0px 0px 20px 0px;
      grid-area: name;
   }

   &__phone {
      margin: 0px 0px 35px 0px;
      grid-area: phone;
   }

   &__signature-adress {
      margin: 0px 0px 35px 0px;
      grid-area: signAdress;
   }

   &__adress {
      margin: 0px 0px 35px 0px;
      grid-area: adress;
   }

   &__signature-payment {
      grid-area: signPay;
   }

   &__payment-online {
      grid-area: online;

      @media (max-width: 471px) {
         margin: 0px 0px 20px 0px;
      }
   }

   &__payment-onplace {
      grid-area: onplace;
   }

   &__signature {
      margin: 0px 0px 15px 0px;
   }

   &__return-info {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr;
      column-gap: 40px;
      grid-template-areas:
         "signDate signDate"
         "date time"
         "returnAdress returnAdress"
         "rules rules";

      @media (max-width: 471px) {
         grid-template-areas:
            "signDate signDate"
            "date date"
            "time time"
            "returnAdress returnAdress"
            "rules rules";
      }
   }

   &__signature-date {
      grid-area: signDate;
   }

   &__return-date {
      margin: 0px 0px 20px 0px;
      grid-area: date;
   }

   &__return-time {
      grid-area: time;

      @media (max-width: 471px) {
         margin: 0px 0px 20px 0px;
      }
   }

   &__return-adress {
      margin: 0px 0px 20px 0px;
      grid-area: returnAdress;
   }

   &__return-rules {
      grid-area: rules;
      border: 1px solid #DDE3EB;
      border-radius: 15px;
      padding: 18px;

      @media (max-width: 471px) {
         display: none;
      }
   }

   &__rules-title {}

   &__rules-text {
      &:not(:last-child) {
         margin-bottom: 25px;
      }
   }

   &__insideWrapper {
      height: 60px;
      padding: 20px;
      display: flex;
      justify-content: start;
      align-items: center;
      cursor: pointer;
   }
}

.requestRadioBut {

   &__radio-body {
      display: flex;
      justify-content: start;
      align-items: center;
      cursor: pointer;
   }

   &__button {
      margin-right: 20px;
      width: 32px;
      height: 32px;
      border: 1px solid #DDE3EB;
      border-radius: 50%;
      padding: 5px;
   }

   &__button-active {
      position: relative;

      &::before {
         content: "";
         position: absolute;
         top: 4px;
         left: 4px;
         width: 22px;
         height: 22px;
         border-radius: 50%;
         background-color: #297FFF;
      }
   }

   &__title {}
}