.requestProcces {

   &__body{
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: center;
      width: 100%;
   }

   &__request-info {
      height: 100%;
   }

   &__title {
      margin: 0px 0px 25px 0px;
   }

   &__date {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: start;
      margin: 0px 0px 50px 0px;

      @media (max-width: 631px) {
         flex-wrap: wrap;
         justify-content: center;
      }
   }

   &__swipe {
      display: none;

      @media (max-width: 767px) {
         display: block;
         align-self: end;
         margin: 0px 0px 10px 0px;
      }
   }

   &__column {
      @media (max-width: 631px) {
         text-align: center;
         flex: 1 1 50%;
         margin: 0px 0px 20px 0px;

         &:nth-child(1) {
            order: 1;
         }

         &:nth-child(2) {
            order: 3;
         }

         &:nth-child(3) {
            order: 4;
         }

         &:nth-child(4) {
            order: 2;
         }
      }
   }

   &__signature {
      margin: 0px 0px 3px 0px;
   }

   &__data {}

   &__request-info {
      min-width: 688px;
      width: 100%;
   }

   &__item-title {
      display: flex;
      margin: 0px 0px 20px 0px;
   }

   &__columnName {
      margin-right: 30px;

      &:first-child {
         flex: 1 1 auto;
         margin-left: 100px;
      }

      &:nth-child(4) {
         margin-right: 32px;
      }

      &:last-child {
         margin-right: 30px;
      }
   }

   &__item {
      &:not(:last-child) {
         margin-bottom: 20px;
      }
   }

   &__summary {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      @media (max-width: 471px) {
         flex-direction: column-reverse;
      }
   }

   &__back {
      width: 160px;
      height: 40px;
   }

   &__cashSum {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
      gap: 15px;
      grid-template-areas:
         "titleDelivery costDelivery"
         "titleSum costSum";

      @media (max-width: 471px) {
         margin: 0px 0px 30px 0px;
      }
   }

   &__cashItem {
      display: flex;
      justify-content: end;
      align-items: center;
   }

   &__titleDelivery {
      grid-area: titleDelivery;
   }

   &__titleSum {
      grid-area: titleSum;
   }

   &__costDelivery {
      grid-area: costDelivery;

   }

   &__costeSum {
      grid-area: costSum;
   }
}