.MyCheckBox{display:flex;justify-content:start;align-items:center;position:relative;cursor:pointer}.MyCheckBoxCheck{position:absolute;opacity:0}.MyCheckBoxCheckFake{flex:0 0 auto;position:relative;width:32px;height:32px;background:#fff;border:1px solid #dde3eb;border-radius:10px}.MyCheckBoxCheckFake::before{content:"";color:#fff;position:absolute;width:32px;height:32px;background-color:#297fff;border-radius:10px;display:flex;justify-content:center;align-items:center;opacity:0}.MyCheckBoxCheck:checked+.MyCheckBoxCheckFake::before{opacity:1}