.MyButton {
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   border: none;
   border-radius: 10px;
   color: #fff;
   background-color: #297FFF;
   transition: all 0.2s ease-in-out;
   &:hover {
      background-color: #005EFF;
   }
}