.rental {

   &__wrapper {
      height: 100%;
   }

   &__body {
   }

   &__date-block {
      display: flex;
   }

   &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: -30px;
      margin-right: -95px;
      @media (max-width: 769px) {
         border-top-right-radius: 20px;
         margin-top: 0px;
         margin-right: 0px;
         overflow: hidden;
      }
   
      @media (max-width: 631px) {
         flex-direction: column;
      }
   }

   &__titel-line {
      display: flex;
      align-items: center;
      justify-content: space-between;
      align-items: center;
      max-width: 550px;
      @media (max-width: 631px) {
         margin: 0px 0px 25px 0px;
      }
   }

   &__title {}

   &__accessories {
      display: flex;
      justify-content: end;
      align-items: center;
      width: 680px;
      height: 230px;
      @media (max-width: 769px) {
         width: 710px;
         height: 150px;
         margin-right: -60px;
      }
      @media (max-width: 631px) {
         width: 100%;
         height: 90px;
         margin-right: 0px;
      }
   }

   &__bikeType {
      position: relative;
      
         & span {
            position: absolute;
            top: -65px;
            left: -10px;
         }
   }

   &__selectedTypeBike {
      display: none;
   }

   &__selectedTypeBike-active {

   }
}

.accessories {

   &__item {
      padding-top: 35px;
      padding-left: 45px;
      padding-bottom: 20px;
      height: 100%;
      width: 100%;
      border-bottom-left-radius : 40px;
      border-top-left-radius: 20px;
      transform: skew(-25deg, 0deg);
      display: flex;
      align-items: end;
      &:nth-child(1) {
         background-color: rgb(214, 229, 255);
      }
   
      &:nth-child(2) {
         background-color: rgb(185, 213, 255);
         margin-left: -40px;
      }
   
      &:nth-child(3) {
         background-color: rgb(165, 199, 250);
         margin-left: -40px;
         @media (max-width: 631px) {
            width: 80%;
         }
      }
      @media (max-width: 1025px) {
         padding-top: 10px;
         padding-left: 25px;
         padding-bottom: 30px;
      }
      @media (max-width: 769px) {
         padding-top: 20px;
         padding-left: 25px;
         padding-bottom: 10px;
         border-bottom-left-radius: 20px;
         border-top-left-radius: 10px;
      }
      @media (max-width: 631px) {
         transform: skew(0deg, 0deg);
         padding: 15px;
         align-items: center;
         border-radius: 20px;
      }
   }

   &__body {
      transform: skew(25deg, 0deg);
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;
      @media (max-width: 769px) {
         justify-content: center;
      }
      @media (max-width: 631px) {
         transform: skew(0deg, 0deg);
         justify-content: center;
         align-items: start;
      }
   }
   
   &__image {
      width: 100%;
      height: 100%;
      @media (max-width: 769px) {
         width: 60%;
         height: 60%;
      }
      @media (max-width: 631px) {
         width: 50%;
         height: 50%;
      }
   }

   &__name {
      @media (max-width: 631px) {
         display: none;
      }
   }

   &__cost {
      color: #3E80F7;
      font-size: 16px;
      font-weight: 700;
   }
}