.orderedBike {

   &__body {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   &__image {
      margin-right: 40px;
      background-color: #fff;
      padding: 5px;
      border-radius: 5px;
      & img {
         width: 50px;
         height: 30px;
      }
   }

   &__bikeName {
      flex: 1 1 auto;
      margin-right: 40px;
   }

   &__accessories {
      margin-right: 50px;
   }

   &__price {

   }
}