.application__title{margin:0px 0px 35px 0px}.application__subtitle{margin:0px 0px 10px 0px}.application__name{margin:0px 0px 20px 0px;width:100%}.application__number{width:100%;margin:0px 0px 20px 0px}.application__personal-info{margin:0px 0px 20px 0px;display:flex;justify-content:start;align-items:center;position:relative;cursor:pointer}.application__check{position:absolute;opacity:0}.application__fake-checkbox{flex:0 0 auto;position:relative;width:32px;height:32px;background:#fff;border:1px solid #dde3eb;border-radius:10px}.application__fake-checkbox::before{content:"";color:#fff;position:absolute;width:32px;height:32px;background-color:#297fff;border-radius:10px;display:flex;justify-content:center;align-items:center;opacity:0}.application__check:checked+.application__fake-checkbox::before{opacity:1}.application__confirmation{margin-left:15px;line-height:100%}.application__button{height:60px;max-width:560px;position:relative}.application__submition{position:absolute;bottom:-30px;left:50%;transform:translate(-50%, 0%);width:100%;text-align:center}.application__refused{position:absolute;bottom:-30px;left:50%;transform:translate(-50%, 0%);width:100%;text-align:center}