.mainPage {

   &__wrapper {}

   &__body {
      position: relative;
   }

   &__title {
      padding: 20px;
      border-radius: 15px;
      position: absolute;
      bottom: 20%;
      left: 5%;
      background-color: rgba(255, 255, 255, 0.687);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;
   }

   &__text {
      margin: 0px 0px 20px 0px;
      font-weight: 600;
      font-size: 50px;
      line-height: 100%;
      color: #297FFF;

   }

   &__button {
      width: 260px;
      height: 60px;
   }

   &__back-image {
      & img {
         border-radius: 10px;
         width: 100%;
         height: 80%;
      }
   }
}